/*----------------------------------------
	共通部分
----------------------------------------*/
@use "setting" as *;

html {
  font-size: 100%;
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: $font_Montserrat, $font_sans,  Arial, "Hiragino Kaku Gothic ProN",
  "Hiragino Sans", Meiryo, sans-serif;
  font-feature-settings: "palt";
  color: $c_text;
  letter-spacing: 0.05em;
}

a {
  text-decoration: none;
}
img {
  max-width: 100%;
  height: auto;
  vertical-align: top;
}

li {
  list-style: none;
}
