/* mv
------------------------------------------------------ */
@use "../../../base/setting"as *;
@use "../../../global/"as *;

.mv {
  background-image: $bg-gradient;
  overflow: hidden;
  position: relative;
}

.mv__inner {
  height: 450px;
  max-width: 1240px;
  padding: 0 30px;
  margin: 0 auto;
  display: flex;
  @include mq(md) {
    height: 700px;
    padding: 0 50px;
  }
}

.mv__title-area {
  @include z-index(default);
  padding-top: 75px;
  @include mq(md) {
    padding-top: 156px;
  }
}

.mv__title-area::after {
  content: "";
  background-image: url(../../images/teiji-de-kaerumon.svg);
  display: inline-block;
  background-size: contain;
  position: absolute;
  bottom: 35%;
  left: 48%;
  width: 153px;
  height: 64px;
  @include mq(sm) {
    bottom: 29%;
    left: 51%;
  }
  @include mq(md) {
    bottom: 175px;
    left: 382px;
    width: 355px;
    height: 140px;
  }
}

.mv__sub-title {
  line-height: 1.3;
  font-weight: bold;
  color: $c_key1;
  margin-bottom: 0.8em;

  @include mq(md) {
    font-size: rem(24);
    line-height: 1;
  }
}


//タブレット幅以上では改行なし
.mv__sub-title-br {
  @include mq(md) {
    display: none;
  }
}

.mv__title {
  font-size: clamp(rem(24), 10vw, rem(100));
  font-weight: bold;
  line-height: 1;
  margin-bottom: 10px;
}

.mv__date {
  font-size: rem(18);
  font-weight: bold;
  letter-spacing: 0.07em;
  margin-bottom: 30px;
  @include mq(md) {
    font-size: rem(36);
  }
}

.mv__speaker-name {
  position: absolute;
    right: 20px;
    bottom: 20px;
    font-size: rem(12);
    line-height: 1;
    @include mq(md) {
      font-size: rem(21);
    }
}
.mv__speaker-name-label {
  font-size: rem(10);
  @include mq(md) {
    font-size: rem(12);
  }
}

.mv__img {
  position: absolute;
width: 340px;
right: -80px;
top: 3px;
@include mq(md) {
  width:auto;
  right: 0px;
top: -36px;
}
}