/* ボタン
------------------------------------------------------ */
@use "../../../base/setting"as *;
@use "../../../global/"as *;
.btn {
  display: inline-block;
  width: 180px;
  max-width: 100%;
  font-size: rem(16);
  padding: 20px 0;
  background-image: $btn-gradient;
	border-radius: 50px;
  box-shadow: 0 6px 0 #dd118e;
  color: #fff;
  text-align: center;
  text-decoration: none;
  transition: .25s;
  line-height: 1;
  font-weight: bold;
  @include mq(md) {
    width: 300px;
    padding: 24px 10px;
    box-shadow: 0 10px 0 #dd118e;
    font-size: rem(18);
  }
}

.btn:hover {
  transform: translateY(4px);
  box-shadow: 0 6px 0 #dd118e;
}