/*----------------------------------------
	Form Parts
----------------------------------------*/
@use "../../../base/setting"as *;
@use "../../../global/"as *;

/*各フォーム部品の基本スタイルを上書きします*/
input[type="text"],
input[type="email"],
input[type="tel"],
input[type="url"],
input[type="search"],
input[type="number"],
textarea,
select {
  -webkit-appearance: none;
  appearance: none;
  /*ブラウザ標準スタイルを無効にする*/
  width: 100%;
  padding: 8px;
  border-radius: 5px;
  border: 1px solid #aaa;
  font-size: rem(16);
  background-color: $c_white;
}

.form-container {
  padding: 8%;
  border: 5px solid $c_key1;
  border-radius: 20px;
  background-color: #1a1a1a;
  @include mq(md) {
    padding: 90px;
  }
}

.form__item {
  margin-bottom: 30px;
}

.form__ttl {
  display: inline-block;
  font-weight: bold;
  margin-bottom: 10px;
}

.form__body {
  width: 100%;
}

input[type="radio"]+span {
  margin-right: 35px;
  padding-left: 5px;
}

/*入力フィールド
---------------------------------*/
.inputField {
  @include mq(md) {
  width: 300px;
  }
}

.inputField--tel {
  @include mq(md) {
    width: 200px;
    }
}

.inputField--message {
  width: 100%;
  min-width: 100%;
  @include mq(md) {
  min-width: 600px;
  min-height: 200px;
  }
}

/*補足説明文
---------------------------------*/
.form__note {
  margin-top: 5px;
  color: #999;
  font-size: 0.8em;
}

/*水平ラジオボタン選択肢リスト
---------------------------------*/
.radioList {
  @include mq(md) {
  display: flex;
  align-items: center;
  }
}

/*セレクトボックス
------------------------------------*/
select {
  -webkit-appearance: none;
  appearance: none;
  display: block;
  width: 100%;
  padding: 10px 30px 8px 10px;
  border-radius: 4px;
  border: 1px solid #ccc;
}

/*必須・任意ラベル
---------------------------------*/
.label-required {
  font-size: rem(12);
  color: $c_key1;
  margin-left: 10px;
}