/* table
------------------------------------------------------ */
@use "../../../base/setting"as *;
@use "../../../global/"as *;


.table__inner {
  border-collapse: collapse;  /*セル同士の間に隙間を開けない*/
  width: 100%;
}


.table__header {
  width: 200px;
  font-size: rem(22);
  padding: 40px 30px;
  border-bottom: 1px solid $c_key1;
  font-weight: bold;
  vertical-align: top;
  text-align: left;
}

.table__header-br {
  @include mq(md) {
    display: none;
  }
}

.table__text {
  padding: 40px 30px;
  border-bottom: 1px solid $c_key1;
}

.table__title {
  font-size: rem(22);
    font-weight: bold;
    color: $c_key1;
    margin-bottom: 15px;
}

.table__sentence {
  font-size: rem(15);
  line-height: 1.5;
}
