/* section
------------------------------------------------------ */
@use "../../../base/setting"as *;
@use "../../../global"as *;

.section__inner {
  padding: 50px 0;
  @include mq(md) {
    padding: 110px 0;
  }
}

.section__title {
  text-align: center;
  margin-bottom: 20px;
  font-weight: bold;
  line-height: 1;

  @include mq(md) {
    margin-bottom: 60px;
  }
}

.section__title-main {
  font-size: rem(32);
  margin-bottom: 5px;
  line-height: 1;
  letter-spacing: 0.05em;
  color: $c_key1;
  text-transform: uppercase;

  @include mq(md) {
    font-size: rem(60);
    margin-bottom: 10px;
  }
}

.section__title-sub {
  font-size: rem(14);
  letter-spacing: 0.05em;
  @include mq(md) {
    font-size: rem(20);
  }
}

.section-about,.section-session {
  background-color: $bg-color;
  margin-left: calc(50% - 50vw);
  margin-right: calc(50% - 50vw);
  padding-left: calc(50vw - 50%);
  padding-right: calc(50vw - 50%);
}

.section-speaker {
  background-image: $bg-gradient;
  margin-left: calc(50% - 50vw);
  margin-right: calc(50% - 50vw);
  padding-left: calc(50vw - 50%);
  padding-right: calc(50vw - 50%);
}

.section-registor {
  background-image: url(../../images/bg.png);
  background-repeat: repeat;
  margin-left: calc(50% - 50vw);
  margin-right: calc(50% - 50vw);
  padding-left: calc(50vw - 50%);
  padding-right: calc(50vw - 50%);
}